import { Component } from '@angular/core';
import { IonicPage, NavController, Platform } from 'ionic-angular';
import { RestProvider } from '../../../providers/rest/rest';
import 'rxjs/add/operator/map'

@IonicPage()
@Component({
  selector: 'page-listacursos',
  templateUrl: 'listacursos.html',
})
export class ListacursosPage {
    
    pet: string = "puppies";
    isAndroid: boolean = false;
    cursos: any;
    searchTerm: string = '';
    items: any;
   

  constructor(public navCtrl: NavController, platform: Platform, 
    public restProvider: RestProvider) {
      this.isAndroid = platform.is('android');
      
      this.getCursos();      
      
  }

  goToSingleCurso(i){        
    console.log(this.cursos[i].FK0011ProgramacaoCurso);
    console.log(this.cursos[i]);
    this.navCtrl.push('SinglecursoPage', {singleCursos:this.cursos[i]})
  }
  
  getCursos() {
    this.restProvider.getRest('{"operacao":"getcursos"}')
    .then(data => {
      console.log(data);
    this.cursos = data;
        });
    }
    goToPesquisa(){      
      this.navCtrl.push('PesquisacursoPage')

    }
    
    filterItems(searchTerm){

        return this.cursos.filter((item) => {
            return item.AD0013TurmaNome.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
        });
    } 
    setFilteredItems() {

        this.items = this.filterItems(this.searchTerm);

    }

}
export const snapshotToArray = snapshot => {
  let returnArr = [];

  snapshot.forEach(childSnapshot => {
    let item = childSnapshot.val();
    item.key = childSnapshot.key;
    returnArr.push(item);
  });

  return returnArr;
};




